import React, { useMemo } from 'react';

// material-ui
import { makeStyles } from '@material-ui/styles';
import { Box, Button, FormControl } from '@material-ui/core';

// third party

import * as Yup from 'yup';

// project imports
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider, RHFTextField } from '../../../components/hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { forgetPassword } from '../../../store/actions/AuthActions/forgetPassword.action';

// style constant
const useStyles = makeStyles((theme) => ({
    loginInput: {
        ...theme.typography.customInput
    }
}));

//============================|| Forgot Password ||============================//

const RestForgotPassword = (props, { ...others }) => {
    const classes = useStyles();
    const RegisterSchema = Yup.object().shape({
        email: Yup.string().email('Email must be a valid email address').required('Email is required')
    });
    const dispatch = useDispatch();
    const defaultValues = {
        email: ''
    };
    const { loading } = useSelector(({ login }) => login);

    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(RegisterSchema),
        defaultValues
    });

    const {
        handleSubmit,
        formState: { errors }
    } = methods;

    const onSubmit = ({ email }) => {
        dispatch(forgetPassword(`users-service/users/${email}/password/forgot`));
    };

    const emailError = useMemo(() => errors?.email?.message, [errors?.email?.message]);

    return (
        <React.Fragment>
            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                <FormControl fullWidth error={Boolean(emailError)} className={classes.loginInput}>
                    <RHFTextField
                        name="email"
                        placeholder="Enter your email"
                        id="outlined-adornment-email-login"
                        type="email"
                        autoFocus
                        inputProps={{
                            classes: {
                                notchedOutline: classes.notchedOutline
                            }
                        }}
                    />
                </FormControl>

                <Box
                    sx={{
                        mt: 2
                    }}
                >
                    <AnimateButton>
                        <Button
                            disableElevation
                            disabled={loading}
                            fullWidth
                            size="large"
                            type="submit"
                            variant="contained"
                            color="primary"
                        >
                            Send
                        </Button>
                    </AnimateButton>
                </Box>
            </FormProvider>
        </React.Fragment>
    );
};

export default RestForgotPassword;
