import { http } from '../../../utils/http';
import { FORGET_PASSWORD_START, FORGET_PASSWORD_SUCCESS, FORGET_PASSWORD_ERROR } from '../../constants';
import { setMessage } from '../MessageActions';

const forgetPasswordStart = () => {
    return { type: FORGET_PASSWORD_START };
};
const forgetPasswordSuccess = (payload) => {
    return { type: FORGET_PASSWORD_SUCCESS, payload };
};
const forgetPasswordError = (error) => {
    return { type: FORGET_PASSWORD_ERROR, error };
};

export const forgetPassword = (url) => (dispatch, getState) => {
    dispatch(forgetPasswordStart());
    try {
        http(process.env.REACT_APP_ROOT_URL, 'POST', url, {}, false)
            .then((res) => {
                dispatch(forgetPasswordSuccess(res));
                dispatch(setMessage(res.message));
            })
            .catch((err) => {
                dispatch(forgetPasswordError(err?.message));
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(forgetPasswordError('Something went wrong'));
    }
};
